<template>
  <div id="navbar">
    <nav class="navbar navbar-expand-lg navbar-light bg-light">
      <div class="container-fluid">
        <router-link to="/" class="navbar-brand">Employee Management App</router-link>
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarNavAltMarkup"
          aria-controls="navbarNavAltMarkup"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
          <div class="navbar-nav">
            <router-link v-if="isLoggedIn" to="/dash" class="nav-item nav-link">Dashboard</router-link>
          </div>
          <div class="navbar-nav ml-auto">
            <div class="navbar-nav">
              <router-link v-if="!isLoggedIn" to="/login" class="nav-item nav-link">Login</router-link>
              <router-link v-if="!isLoggedIn" to="/register" class="nav-item nav-link">Register</router-link>
              <span class="nav-item nav-link" v-if="isLoggedIn">{{currentUser}}</span>
              <a class="nav-item nav-link">
                <button
                  v-if="isLoggedIn"
                  class="btn btn-outline-secondary btn-sm"
                  @click="logout"
                >Logout</button>
              </a>
            </div>
          </div>
        </div>
      </div>
    </nav>
  </div>
</template>
<script>
import firebase from "firebase";
export default {
  data() {
    return {
      name: "navbar",
      isLoggedIn: false,
      currentUser: false
    };
  },
  methods: {
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.go({
            path: this.$router.path
          });
        });
    }
  },
  created() {
    if (firebase.auth().currentUser) {
      (this.isLoggedIn = true),
        (this.currentUser = firebase.auth().currentUser.email);
    }
  }
};
</script>
