<template>
  <div class="container">
    <div class="dashboard">
      <div class="text-center">
        <h1>DASHBOARD</h1>
      </div>
      <hr>
      <div class="container">
        <div class="d-flex justify-content-between">
          <h2>employees</h2>
          <router-link to="/new" class="btn btn-outline-primary">+</router-link>
        </div>

        <div class="table-container">
          <table class="table table-bordered table-hover my-2">
            <thead>
              <tr>
                <th scope="col">Name</th>
                <th scope="col">Department</th>
                <th scope="col">Position</th>
                <th scope="col">Action</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="employee in employees" :key="employee.id">
                <td>{{employee.name}}</td>
                <td>{{employee.dept}}</td>
                <td>{{employee.position}}</td>
                <td>
                  <router-link
                    :to="{name:'show-employee',params:{employee_id: employee.employee_id}}"
                  >view</router-link>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "Dashboard",
  data() {
    return {
      employees: []
    };
  },

  created() {
    db.collection("employees")
      .orderBy("dept")
      .get()
      .then(querySnapshot => {
        //const employees = [];
        querySnapshot.forEach(doc => {
          const data = {
            id: doc.id,
            employee_id: doc.data().employee_id,
            name: doc.data().name,
            dept: doc.data().dept,
            position: doc.data().position
          };
          this.employees.push(data);
        });
      });
  }
};
</script>
