<template>
  <div class="employee">
    <h1>New Employee</h1>
    <div class="add_new_product d-flex justify-content-center">
      <form class="needs-validation" novalidate>
        <div class="form-group">
          <label for="employee_id">Id</label>
          <input
            type="number"
            class="form-control"
            id="employee_id"
            aria-describedby="employee_id_help"
            placeholder="Id"
            v-model="employeeData.employee_id"
            required
          >
        </div>
        <div class="form-group">
          <label for="employee_name">Name</label>
          <input
            type="text"
            class="form-control"
            id="employee_name"
            aria-describedby="employee_name_help"
            placeholder="Name"
            v-model="employeeData.name"
            required
          >
        </div>
        <div class="form-group">
          <label for="employee_position">Position</label>
          <input
            type="text"
            class="form-control"
            id="employee_position"
            aria-describedby="employee_position_help"
            placeholder="Position"
            v-model="employeeData.position"
          >
        </div>
        <div class="form-group">
          <label for="employee_department">Department</label>
          <select class="form-control" id="employee_department" v-model="employeeData.dept">
            <option v-for="dept in departments" :key="dept" :selected="dept =='Sales'">{{dept}}</option>
          </select>
        </div>

        <p @click="addEmployee" class="btn btn-primary">Submit</p>
      </form>
    </div>
  </div>
</template>

    <script>
import db from "../firebaseInit";
export default {
  data() {
    return {
      employeeData: {
        employee_id: null,
        position: null,
        name: null,
        dept: null
      },
      departments: [" Algebra", "IT", "Sales"],
      employees: []
    };
  },
  methods: {
    addEmployee() {
      db.collection("employees")
        .add({
          employee_id: this.employeeData.employee_id,
          position: this.employeeData.position,
          name: this.employeeData.name,
          dept: this.employeeData.dept
        })
        .then(docRef => {
          this.$router.push("/");
        })
        .catch(error => console.log(error));
    }
  }
};
</script>
    <style scoped>
.add_new_product {
}
</style>
    