<template>
  <div class="container view-employee">
    <div class="em-name">{{name}}</div>
    <div class="em-name">{{position}}</div>
    <div class="em-name">{{dept}}</div>
    <div class="actions">
      <div class="edit">
        <router-link
          :to="{name:'edit-employee',params:{employee_id: employee_id}}"
          class="btn btn-warning"
        >Edit</router-link>
      </div>
      <div class="delete">
        <span @click="deleteEmployee" class="btn btn-danger">Delete</span>
      </div>
    </div>
  </div>
</template>

    <script>
import db from "../firebaseInit";
export default {
  data() {
    return {
      employee_id: null,
      name: null,
      dept: null,
      position: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("employees")
      .where("employee_id", "==", to.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.employee_id = doc.data().employee_id;
            vm.name = doc.data().name;
            vm.position = doc.data().position;
            vm.dept = doc.data().dept;
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      db.collection("employees")
        .where("employee_id", "==", this.$route.params.employee_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.employee_id = doc.data().name;
            this.name = doc.data().name;
            this.dept = doc.data().dept;
            this.position = doc.data().position;
          });
          this.employees = employees;
        });
    },
    deleteEmployee() {
      if (confirm("Are you sure")) {
        db.collection("employees")
          .where("employee_id", "==", this.$route.params.employee_id)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete();
              this.$router.push("/");
            });
          });
      }
    }
  }
};
</script>
