<template>
  <div class="employee">
    <h1>Edit Employee</h1>
    <div class="add_new_product d-flex justify-content-center">
      <form class="needs-validation" novalidate>
        <div class="form-group">
          <label for="employee_name">Name</label>
          <input
            type="text"
            class="form-control"
            id="employee_name"
            aria-describedby="employee_name_help"
            placeholder="Name"
            v-model="name"
            required
          >
        </div>
        <div class="form-group">
          <label for="employee_position">Position</label>
          <input
            type="text"
            class="form-control"
            id="employee_position"
            aria-describedby="employee_position_help"
            placeholder="Position"
            v-model="position"
          >
        </div>
        <div class="form-group">
          <label for="employee_department">Department</label>
          <select class="form-control" id="employee_department" v-model="dept">
            <option v-for="dept in departments" :key="dept" :selected="dept =='Sales'">{{dept}}</option>
          </select>
        </div>

        <p @click="updateEmployee" class="btn btn-primary">Update</p>
      </form>
    </div>
  </div>
</template>

    <script>
import db from "../firebaseInit";
export default {
  name: "edit-employee",
  data() {
    return {
      employee_id: null,
      position: null,
      name: null,
      dept: null,
      departments: [" Algebra", "IT", "Sales"],
      employees: []
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("employees")
      .where("employee_id", "==", to.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.employee_id = doc.data().employee_id;
            vm.name = doc.data().name;
            vm.position = doc.data().position;
            vm.dept = doc.data().dept;
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      db.collection("employees")
        .where("employee_id", "==", this.$route.params.employee_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.employee_id = doc.data().employee_id;
            this.name = doc.data().name;
            this.dept = doc.data().dept;
            this.position = doc.data().position;
          });
          this.employees = employees;
        });
    },
    updateEmployee() {
      db.collection("employees")
        .where("employee_id", "==", this.$route.params.employee_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref
              .update({
                employee_id: this.employee_id,
                name: this.name,
                dept: this.dept,
                position: this.position
              })
              .then(() => {
                this.$router.push({
                  name: "show-employee",
                  params: { employee_id: this.employee_id }
                });
              });
          });
        });
    }
  }
};
</script>
